import React from 'react';
import {  View, ScreenSpinner, Panel, Epic, Button, PanelHeader, Panar, Avatar, Div, HeaderButton, Cell, Snackbar, Gallery, Group} from '@vkontakte/vkui';
import './Profile.css'
// Компонент профиля
class Profile extends React.Component {
	constructor(props) {
       super(props);

       this.state = {
          fetchedUser:this.props.fetchedUser
     }
  }

	render() {
		return(
                <Div id="profile-container" align="center">
                     <Avatar src={this.state.fetchedUser.photo_200} size={200}/>
                  <Div id="data-container">
                     <Cell class="data-items" id="first-name">{this.state.fetchedUser.first_name +" "+ this.state.fetchedUser.last_name}</Cell>
                    
                  </Div>
            </Div>
		);           
	}
}

export default Profile;